import {gql} from '@apollo/client'

export const GET_REPORT_DATA_BY_DISTRICT = gql`
query{
    getDataForReport{
             _id
          menConfirmToday
         womenConfirmToday
         menConfirm
         womenConfirm
         
         menRecoveredToday
         womenRecoveredToday
         menRecovered
         womenRecovered
  
         menDeathsToday
         womenDeathsToday
         menDeaths
         womenDeaths
  }
  }
`

export const GET_PEOPLE_CONFIRM = gql`
query getPeopeleConfirmRecoverAndDeath(
  $startDate: DateTime
  $endDate: DateTime
){
    getPeopeleConfirmRecoverAndDeath(
      startDate:$startDate,
      endDate: $endDate
    ){
      confirm{
        id
        firstName
        lastName
        age
        gender
        province
        district
        commune
        village
        other
      }
      recovered{
        id
        firstName
        lastName
        age
        gender
        province
        district
        commune
        village
        other
      }
      death{
        id
        firstName
        lastName
        age
        gender
        province
        district
        commune
        village
        other
        currentState{
          deathAt
          
        }
      }
    }
  }
`

export const GET_REPORT_DATA_BY_LOCATION = gql`
query{
    affectedLocationReport{
        totalAffectedLocation
        totalAffectedLocationToday
        totalAffectedLocationNotClosed
        totalAffectedLocationOn
        closedLocation
        coorporateLocation
        coorporateLocationToday
        openedLocation
        openedLocationToday
        closedLocationToday
    }
  }
`